// 获取所有社团活动
export const getAllClubs = () => clubs;

// 获取特色社团活动
export const getFeaturedClubs = () => clubs.filter(club => club.featured);

// 按分类获取社团活动
export const getClubsByCategory = (category) => 
  clubs.filter(club => club.category === category);

// 获取社团活动详情
export const getClubById = (id) => 
  clubs.find(club => club.id === id);

// 获取所有分类
export const getAllCategories = () => {
  const categories = [...new Set(clubs.map(club => club.category))];
  return categories;
};