    description: ' Attending Penn M&TSI program was a transformative experience where I collaborated with talented peers to develop an award-winning smart sleep mask, gaining hands-on skills in both engineering and business innovation. I was fortunate to be admitted to the University of Pennsylvania’s Management & Technology Summer Institute (M&TSI), a highly selective program that brings together about 75 students from around the world to explore the intersection of business and engineering. Immersed in both the Wharton School and Penn Engineering curriculum, I studied topics such as product design, go-to-market strategy, venture capital pitching, and innovation management, while also learning directly from guest speakers from companies like Apple, Tesla, and fast-growing startups.During the program, I led the engineering work for MallowMask, a smart sleep mask designed for quiet and shared environments. It uses internal LEDs to imitate natural sunrise, gentle vibration alerts, and thermal elements to create a silent and comfortable waking experience. I worked on Arduino programming to control the LED sunrise simulation and timed vibration, sketched early prototypes in Procreate, and refined the final model in CAD. Our team also developed a complete business strategy using TAM–SAM–SOM market sizing and SWOT analysis, and I helped craft the storyboard and film the final product video. Our project received the Best Product Prize, which was an incredible moment of recognition for our collaboration and creativity.Beyond the academics, what made M&TSI unforgettable were the people. I met some of the most inspiring, curious, and passionate peers I’ve ever known. We shared ideas late into the night in the lab, helped each other debug code, and sometimes stayed until 2 a.m. perfecting our prototypes. Between all the hard work, we went out for hotpot, sang karaoke after long days, went to Hershy Park, and laughed until we lost track of time.To actually study on Penn’s campus, attend lectures in Wharton classrooms, and build in real engineering labs was both surreal and deeply motivating. M&TSI gave me not only the skills to connect technology with business but also a glimpse into a community where innovation thrives through collaboration and curiosity. It remains one of the most meaningful and transformative experiences I’ve ever had.',
